.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    background-color: #fff;
    border-bottom: 1px solid #eaeaea;
}

.header .logo {
    font-size: 24px;
    font-weight: bold;
}

.header nav ul {
    list-style: none;
    display: flex;
    gap: 15px;
}

.header nav ul li a {
    text-decoration: none;
    color: #333;
}

.header .profile {
    display: flex;
    align-items: center;
    gap: 10px;
}

.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    background-color: #fff;
    border-bottom: 1px solid #eaeaea;
    transition: all 0.3s ease;
}

