.leaderboard-card {
    position: relative;
    background-color: #1c1c1e;
    border-radius: 20px;
    padding: 20px;
    margin-bottom: 20px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
    color: white;
    text-decoration: none;
    overflow: hidden;
}

.leaderboard-card:hover {
    transform: translateY(-5px);
    box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.2);
}

.leaderboard-card::before {
    content: '';
    position: absolute;
    top: -50%;
    left: -50%;
    width: 200%;
    height: 200%;
    background: radial-gradient(circle, rgba(255, 255, 255, 0.2) 0%, rgba(0, 0, 0, 0) 60%);
    transition: all 0.5s;
    z-index: 0;
}

.leaderboard-card:hover::before {
    top: -30%;
    left: -30%;
    transition: all 0.5s;
}

.card-content {
    position: relative;
    z-index: 1;
}

.card-header {
    display: flex;
    
    align-items: center;
    margin-bottom: 10px;
    color: #fff;
}

.rank {
    font-size: 1.2rem;
    font-weight: bold;
    color: #007bff;
}

.name {
    font-size: 1.2rem;
    font-weight: bold;
}

.card-details {
    margin-bottom: 10px;
    color: #aaa;
}

.card-actions {
    text-align: right;
}

.view-button {
    padding: 8px 16px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease-in-out;
}

.view-button:hover {
    background-color: #0056b3;
}

